import React, { useEffect, useState } from "react";
import { Typography } from "./base/typography";

const start = new Date("04/2/2022 2:00 PM").getTime();
const finish = new Date("04/3/2022 2:00 PM").getTime();

const formatTime = (timeMs: number) => {
	const time = Math.floor(timeMs / 1000);
	const hours = Math.floor(time / 3600);
	const minutes = Math.floor((time % 3600) / 60);
	const seconds = time % 60;
	return `${hours.toString().padStart(2, "0")}:${minutes.toString().padStart(2, "0")}:${seconds
		.toString()
		.padStart(2, "0")}`;
};

const Countdown: React.FC = () => {
	const [state, setState] = useState("Starting in");
	const [time, setTime] = useState(0);
	useEffect(() => {
		const interval = setInterval(() => {
			const startDelta = start - new Date().getTime();
			if (startDelta > 0) {
				setState("Starting in");
				setTime(startDelta);
			} else {
				const finishDelta = finish - new Date().getTime();
				if (finishDelta > 0) {
					setTime(finishDelta);
					setState("Closing in");
				} else {
					setTime(0);
					clearInterval(interval);
				}
			}
		}, 500);
		return () => clearInterval(interval);
	}, [time]);
	return (
		<div>
			<Typography style={{ fontSize: "2em", paddingTop: "0.5em" }}>{state}</Typography>
			<Typography style={{ fontSize: "5em", padding: "0", lineHeight: 0.9 }}>
				<b>
					<u>{formatTime(time)}</u>
				</b>
			</Typography>
		</div>
	);
};

export default Countdown;
