import React from "react";

type Props = {
	style?: React.HTMLAttributes<HTMLTextAreaElement>["style"];
	children?: React.ReactNode;
};

export const Typography: React.FC<Props> = (props) => {
	return (
		<div
			style={{
				color: "white",
				fontFamily: "Chakra Petch",
				...props.style,
			}}
		>
			{props.children}
		</div>
	);
};
